body {
    --tw-bg-opacity: 1;
    background-color: rgb(15 23 42/var(--tw-bg-opacity)) !important;
}

.manu-container {
    margin-left: 23rem;
    margin-right: 23rem;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.281);
    padding-bottom: 10rem;
    margin-bottom: 3rem;
}

/* Modificaciones específicas para pantallas pequeñas */
@media only screen and (max-width: 600px) {
    .manu-container {
      padding: 3px;
      margin: 1rem;
      
    }
    .manu-title{
        font-size: 35px !important;
        text-align: center;
        margin-top: 5rem !important;
    }
    .manu-container h2{
        font-size: 25px;
        text-align: center;
    }
    .about-me, .experience, .job-duty {
        font-size: 25px;
    }
    .business-name{
        font-size: 25px;
    }

    .info-container{
        margin: 1.2rem !important;
    }

    .job-time{
        font-size: 20px !important;
    }
  }

/* Modificaciones específicas para iPads */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .manu-container {
      padding: 3px;
      margin: 1rem;
    }
    .manu-title {
        font-size: 60px !important;
        margin-top: 4rem !important;
        text-align: center;
    }
    .manu-subtitle{
        font-size: 20px;
        text-align: center;
    }
    .info-container{
        margin: 2rem !important;
    }
  }

.manu-container h1, h2, h3, h4, p, span, strong {
    font-family: 'Oswald';
}

.info-container{
    margin: 5rem;
}

.manu-title {
    margin-top: 8rem;
    --tw-text-opacity: 1;
    color: rgb(94 234 212/var(--tw-text-opacity));
    font-size: 70px;
    margin-bottom: 0;
    font-style: inherit;
}

.manu-subtitle{
    --tw-text-opacity: 1;
    color: rgb(94 234 212/var(--tw-text-opacity));
    margin-top: 0;
    font-size: 44px;
}

.title-animate {
    text-align: left;
}

.description {
    --tw-text-opacity: 1;
    color: rgb(148 163 184/var(--tw-text-opacity));
    text-align: justify;
    font-size: 18px;
}

.about-me{
    margin-top: 3rem;
    --tw-text-opacity: 1;
    color: rgb(226 232 240/var(--tw-text-opacity));
}

.experience{
    margin-top: 6rem;
    --tw-text-opacity: 1;
    color: rgb(226 232 240/var(--tw-text-opacity));
}

/* EXPERIENCIA */

.card-container {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    display: flex;
    flex-wrap: wrap;
    color: #fff;
    justify-content: space-between;
}

.job-card{
    --tw-text-opacity: 1;
    overflow: hidden;
    text-align: center;
    flex: 1;
    border-bottom: 3px solid rgb(94 234 212/var(--tw-text-opacity));
}
.business-name {
    --tw-text-opacity: 1;
    color: rgb(94 234 212/var(--tw-text-opacity));
    margin-top: 3rem;
}

.job-time {
    --tw-text-opacity: 1;
    color: rgb(148 163 184/var(--tw-text-opacity));
    font-size: 18px;
}

.job-description {
    --tw-text-opacity: 1;
    color: rgb(148 163 184/var(--tw-text-opacity));
    text-align: justify;
    font-size: 18px;
}

.job-duty {
    text-align: left;
    font-size: 24px;
}

/* FIN EXPERIENCIA */

  .flex-wrap {
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 7rem;
  }

  .aptitudes-li {
    margin-top: 0.5rem;
    margin-right: 0.375rem;
  }

  .aptitudes-box {
    --tw-text-opacity: 1;
    color: rgb(94 234 212/var(--tw-text-opacity));
    line-height: 1.25rem;
    font-weight: 500;
    font-size: .95rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    background-color: rgba(45,212,191,.1);
    border-radius: 9999px;
    align-items: center;
    display: flex;
  }